import React, { ReactNode } from 'react';
import { isNumber } from 'lodash-es';
import { ArrowRightNormal, CalendarBetaNormalNormal } from '@ali/we-design';
import paragraphFactory, { BaseParagraphProps } from '../paragraphFactory';
import { CALENDAR_CARD_ICON_WIDTH, CALENDAR_CARD_EXTRA_ICON_WIDTH, DEFAULT_ICON_STYLE } from '../../constants';
import { CommonMouseEvent } from '../../type';
import preventDefaultOnClick from '../../uitls/preventDefaultOnClick';
import { ContainerWrapper, IconWrapper, TextWrapper } from './styled';

interface IProps extends BaseParagraphProps {
  jumpToCalendar?: CommonMouseEvent;
  subject?: string | ReactNode;
  isReadonly?: boolean;
  isPlaceholder?: boolean;
}

export const SubjectParagraph: React.FC<IProps> = (props) => {
  const { innerWidth, subject, jumpToCalendar, isReadonly, isPlaceholder } = props;
  const Paragraph = paragraphFactory(<CalendarBetaNormalNormal style={DEFAULT_ICON_STYLE} />);
  const realWidth = isNumber(innerWidth)
    ? (innerWidth - CALENDAR_CARD_ICON_WIDTH - CALENDAR_CARD_EXTRA_ICON_WIDTH * 2)
    : innerWidth;
  return (
    <Paragraph>
      <ContainerWrapper
        width={realWidth}
        isReadonly={isPlaceholder || isReadonly}
      >
        <TextWrapper
          onMouseDown={jumpToCalendar}
          onClick={preventDefaultOnClick}
          data-testid="calendar-subject"
        >
          {subject}
        </TextWrapper>
        <IconWrapper>
          {(isReadonly || isPlaceholder) ? null : <ArrowRightNormal />}
        </IconWrapper>
      </ContainerWrapper>
    </Paragraph>
  );
};
